@font-face {
  font-family: "mlb-primary";
  src: url("fonts/benton-sans-cond-medium.woff2") format("woff2");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "icomoon";
  src: url("fonts/icomoon.eot");
  src: url("fonts/icomoon.eot?#iefix") format("embedded-opentype"), url("fonts/icomoon.woff") format("woff"), url("fonts/icomoon.ttf") format("truetype"), url("fonts/icomoon.svg#icomoon") format("svg");
  font-weight: normal;
  font-style: normal; }

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.icon-newpaper:before {
  content: "\e904"; }

.icon-video:before {
  content: "\e914"; }

body {
  margin: 0;
  font-family: "mlb-primary"; }

header {
  background-color: #002E6D;
  height: 140px;
  padding: 20px;
  color: #FFFFFF; }
  header .logo {
    width: 120px;
    height: auto; }
  header h2 {
    font-weight: normal; }

.game {
  padding: 5px 20px 0; }
  .game__date {
    border-bottom: 1px solid #ECECEC;
    padding-bottom: 10px;
    font-size: 20px;
    color: #333333;
    margin: 15px 0 10px; }
    @media screen and (min-width: 768px) {
      .game__date {
        margin: 30px 0 10px;
        font-size: 25px; } }
  .game__details {
    border-bottom: 1px solid #ECECEC;
    padding: 10px 0; }
    @media screen and (min-width: 768px) {
      .game__details {
        display: flex;
        justify-content: space-between; } }
    .game__details > div {
      display: flex;
      justify-content: space-between;
      font-size: 14px; }
      @media screen and (min-width: 768px) {
        .game__details > div {
          padding: 0;
          font-size: 16px;
          display: inline-flex;
          width: 50%; } }
    .game__details a {
      text-decoration: none;
      color: #000000; }
    .game__details > a {
      display: block; }
      .game__details > a span {
        font-size: 22px;
        color: #666666;
        vertical-align: middle; }
    .game__details-teams {
      width: 60%; }
      .game__details-teams .at-icon {
        color: #666666;
        text-size: 12px;
        padding: 0 5px; }
    .game__details-broadcast {
      width: 40%;
      display: flex;
      justify-content: flex-end; }
      @media screen and (min-width: 768px) {
        .game__details-broadcast {
          justify-content: flex-start; }
          .game__details-broadcast span {
            width: 30%;
            padding-left: 10px; } }
      .game__details-broadcast img {
        max-height: 18px;
        min-height: 16px;
        max-width: 42px;
        height: 100%;
        padding-left: 10px; }
    .game__details-players {
      width: 70%; }
      .game__details-players span {
        padding-right: 10px;
        display: block; }
        @media screen and (min-width: 768px) {
          .game__details-players span {
            display: inline; } }
        .game__details-players span a {
          color: #147CD1;
          display: inline; }
    .game__details-ctas {
      width: 30%;
      display: flex;
      align-items: center;
      justify-content: space-around; }
      .game__details-ctas a {
        color: #666666;
        padding: 0 15px; }
        @media screen and (min-width: 768px) {
          .game__details-ctas a {
            font-size: 20px;
            display: flex;
            align-items: center; } }
      .game__details-ctas .copy {
        display: none; }
        @media screen and (min-width: 768px) {
          .game__details-ctas .copy {
            display: block;
            font-size: 14px;
            padding-left: 10px; } }
  .game__summary {
    font-size: 12px;
    color: #666666; }
  .game__team-logo {
    display: none; }
    @media screen and (min-width: 768px) {
      .game__team-logo {
        display: inline-block;
        height: 20px;
        vertical-align: middle;
        padding-right: 5px; } }
  .game__score {
    padding-left: 5px; }
